:root{
    --white-1: #fff;
    --white-2: #FFFFFFE6;
    --white-3: #F7F7F7;
    --white-4: #DDDDDD;

    --black-1: #222222;
    --black-2: #000000;

    --light-red: #FF385C;
    --gray: #717171;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    line-height: 1.6;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
}

/* ================================ TAG ============================= */

html{
    font-size: 62.5%;
    line-height: 1.6rem;
    font-family: 'Roboto', sans-serif;
}

button{
    border: none;
    outline: none;
}

p{
    margin: 0;
}

/* ================================= CLASS =========================== */
.relative{
    position: relative;
}

.jus-center{
    justify-content: center;
}

.jus-end{
    justify-content: end;
}

.ali-center{
    align-items: center;
}

.h-100{
    height: 100%;
}

.arrow-btn{
    font-size: 1.5rem;
    border: 1.5px solid var(--white-4);
    border-radius: 50%;
    height: 30px;
    width: 30px !important;
    line-height: 30px;
    text-align: center; 
    cursor: pointer;
}

/*=================== grid system =================*/
.row{
    display: flex !important;
    flex-wrap: wrap;
    margin: 0;
}

.row > *{
    width: auto;
    max-width: auto;
    padding: 0;
    margin: 0;  
}

.flex-1{
    flex: 1;
}

.row.flex-column{
    flex-direction: column;
}

.row.flex-nowarp{
    flex-wrap: nowrap;
}

.grid{
    width: 100%;
    display: block;
}

.grid.wide{
    padding: 0 80px;
}

/* mobile */
@media (max-width: 743px){
    .wide{
        padding: 0 24px !important;
    }
}

/* tablet --> PC low resolution */
@media (min-width: 744px) and (max-width: 1127px){
    .wide{
        padding: 0 40px !important;
    }
}
/*=================== grid system =================*/

